function getRoomObject($objOrStr){
	if (typeof $objOrStr == 'object'){ // Already an object, return
		return $objOrStr;
	}

	// Convert it to object
	$objOrStr = $objOrStr.replace(/\'/gi, '\"');
	return JSON.parse($objOrStr);
}

$(document).ready(function(){

	// Resizes the Post Box based on texts inside the textarea
	$('#post_content').autoResize();

	// Enables all tooltips in page
	$(function () {
		$('[data-toggle="tooltip"]').tooltip()
	});

	// Show comments on clicking comments button
	$(document).on('click', '.post-meta > .comments-meta > a', function(e){
		e.preventDefault();
		$(this).parent().parent().parent().find('.all-comments').toggle();
	});

	// Clicking hash links shouldn't take us to the top
	$(document).on('click', 'a[href="#"]', function(e){
		e.preventDefault();
	});


	// Programmmatically click the hidden file input by clicking the link
  $('#AddImageButton').click(function(){
    $('#attached_images').click();
	});
	
	// Clicking the upvote button
	$(document).on('click', '.upvote-button', function(e){
		if ($(this).hasClass('is-upvoted')){
			$(this).find('i').removeClass('fas').addClass('far');
		} else {
			$(this).find('i').removeClass('far').addClass('fas');
		}

		$(this).toggleClass('is-upvoted');
	});

	// Clicking the comment button will show comments and focus comment input
	$(document).on('click', '.comment-button', function(e){
		// $(this).parent().parent().find('.all-comments').show();
		$(this).parent().parent().find('#comment_input').focus();
	});

	// Clicking Edit button will open modal with the post
	$(document).on('click', '.edit-post-option', function(e){
		// console.log($(this).parent().parent().parent().parent().parent().find('.post-body'));
		var $val = $(this).parent().parent().parent().parent().parent().find('.post-body').text().trim();
		// console.log($val);
		$('#EditPost').find('#post_edit_content').val($val);
		$('#EditPost').modal('show');
	});

	// Submit of edit should close the box, AJAX request should be made to send the edit to the server
	$(document).on('click', '#EditPost .btn-submit', function(e){
		e.preventDefault();
		$('#EditPost').modal('hide');
	});

  // Displays image directly from user's filesystem before uploading that to server.
	function DisplayImageBeforeUploading(input) {
		var acceptedTypes = ["image/png", "image/jpeg", "image/gif", "image/webp"]
		if (input.files && input.files[0]) {
			var reader = new FileReader();

			// console.log(input.files[0].type);
			if (acceptedTypes.indexOf(input.files[0].type) < 0){
				alert("The file must be an image file (jpeg/png/gif/webp).");
				return;
			}

			reader.onload = function (e) {
				// We use background image rather than "img" to cover the area
				$('.single-image .the-image').css({
          'background-image' : `url(${e.target.result})`
        });
			}

      reader.readAsDataURL(input.files[0]);
      
      // console.log('adding-class');
			$('.attached-images').addClass('has-images');
		}
  }

	// Clear the form when user removes image from post
  function CancelImageSelection(){
		$('.attached-images').removeClass('has-images'); // Hide the Image Preview
		$('.single-image .the-image').css({ // Image Preview Source to Empty
      'background-image' : `none`
    });
		$('#attached_images').val('') // Clear file input
	}
  
  // Display the image on selecting from filesystem
	$('#attached_images').change(function(){
		DisplayImageBeforeUploading(this);
  });

  // Cancel the image
	$('#CancelUploadButton').on('click', function(){
		CancelImageSelection();
	});

	function DisplayImageInModal(container){
		var imageSource = $(container).find('img').attr('src');
		
		$('#ImageDisplayModal').find('.image-container > img').attr('src', imageSource);
		$('#ImageDisplayModal').modal("show");
	}

	$(document).on('click','.post-images .single-image',function(e){
		DisplayImageInModal(this);
	});

	
	// Generate Posts Dynamically
	if (window.posts !== undefined && window.posts.length > 0){
		window.posts.forEach(function(post){
			var $post = `
	<div class="single-post">
		<div class="post-title bordered-bottom">
			<div class="display-image">
				<a href="#" popover-profileInfo="{'room_id':'room~100','name':'${post.user_name}', 'image':'${post.user_image}', 'color' : '000'}">
					<img src="images/man_avatar1.jpg" alt="DP" />
				</a>
			</div>
			<div class="name-and-time">
				<div class="name">
					<a href="#" popover-profileInfo="{'room_id':'room~100','name':'${post.user_name}', 'image':'${post.user_image}', 'color' : '000'}">${post.user_name}</a>
				</div>
				<div class="time">
					<a href="#">${post.post_time}</a>
				</div>
			</div>
			<div class="post-options">
				<div class="dropleft">
					<button class="btn dropdown" type="button" id="postOptionsMenu" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
						<i class="fas fa-ellipsis-h"></i>
					</button>
					<div class="dropdown-menu" aria-labelledby="postOptionsMenu">
						<a class="dropdown-item edit-post-option" href="#">Edit</a>
					</div>
				</div>
			</div>
		</div>
		<div class="post-body">
			${post.post_content}
		</div>
		<div class="post-images">
			${post.post_images.length > 0 ? `
				<div class="single-image">
					<img src="${post.post_images[0].image_url}" alt="Post Image" />
					<div class="overlay"></div>
				</div>
			` : ``}
		</div>
		<div class="post-options bordered-bottom bordered-top">
			<a href="#" class="btn btn-default btn-option upvote-button ${post.post_is_upvoted ? 'is-upvoted' :''}">
				<i class="${post.post_is_upvoted ? 'fas' :'far'} fa-arrow-alt-circle-up"></i> <!-- or: fas -->
				Upvote
			</a>
			<a href="#" class="btn btn-default btn-option comment-button">
				<i class="far fa-comment-alt"></i>
				Comment
			</a>
		</div>
		<div class="post-meta bordered-bottom">
			<div class="upvotes-meta">
				<a href="#" data-toggle="tooltip" data-placement="top" data-html="true" title="${post.post_liked_by.length > 0 ? post.post_liked_by.join("<br />") : ''}">
					<i class="fas fa-arrow-alt-circle-up"></i>
					${post.post_liked_by.length > 0 ? post.post_liked_by.join(", ") : ''}
				</a>
			</div>
			<div class="comments-meta">
				<a href="#" data-toggle="tooltip" data-placement="top" data-html="true" title="${post.post_comments.length > 0 ? post.post_comments.map(a=>a.user_name).join('<br />') : ''}">
					${post.post_comments.length > 0 ? post.post_comments.length+' comments' : ''}
				</a>
			</div>
		</div>
		<div class="all-comments">`;
		
		if (post.post_comments.length >3){
			var $take_first_3_comments = post.post_comments.slice(0,3);
		} else {
			var $take_first_3_comments = post.post_comments;
		}

		$take_first_3_comments.forEach(function(comment){
			$post += `
			<div class="single-comment">
				<div class="comment-user-image">
					<a href="#">
						<img src="${comment.user_image}" alt="User Image">
					</a>
				</div>
				<div class="name-and-comment">
					<div class="name">
						<a href="#">${comment.user_name}</a> - <small>${comment.comment_time}</small>
					</div>
					<div class="comment">
						${comment.comment_content}
					</div>
				</div>
			</div>
			`;
		});
		if (post.post_comments.length >3){
			$post += `<a href="#" class="more-comments"><div>More Comments...</div></a>`;
		}
		
		$post += `</div>
		<div class="comment-box">
			<div class="user-display-image">
				<img src="images/man_avatar1.jpg" alt="User Display Picture">
			</div>
			<div class="comment-input">
				<textarea class="form-control" name="comment_input" id="comment_input" cols="1" rows="1" placeholder="Write a comment"></textarea>
			</div>
		</div>
	</div>
			`

			$('.the-posts').append($post);
		});
	}




		// Profile Tooltip
		window.shouldHidePopup = true;
		window.shouldShowPopup = true; // Take some time to show the popup
		$(document).on('mouseenter', '[popover-profileInfo]', function(e){
				$this = $(this);
				window.shouldShowPopup = true; // It will show the popup
	
				setTimeout(function(){
						if (!window.shouldShowPopup) // the mouse has left, abort the popup
								return;
	
						$pid = $this.attr('popover-profileInfo');
						// console.log($pid);
						// Do anything with the user id
						// This is the place where we'll update the person details inside the div
						var $person = getRoomObject($pid);
	
						var container = $("#profile-tooltip");
						container.find('.header .name').text($person.name);
						container.find('.actions .send-message').attr('data-room', $pid);
						
						if ($person.self){
							container.find('.actions button').each(function(){
								$(this).hide();
							});
							container.find('.actions .myProfile').show();
						} else {
							container.find('.actions button').each(function(){
								$(this).show();
							});
							container.find('.actions .myProfile').hide();
						}
	
	
						// Image or No Image
						var $yes_image = container.find('.header .yes-image');
						var $no_image = container.find('.header .no-image');
						if ($person.image != 'null'){
							$yes_image.removeClass('display-none');
							$no_image.addClass('display-none');
							$yes_image.attr('src', $person.image);
						} else{
							$yes_image.addClass('display-none');
							$no_image.removeClass('display-none');
							$no_image.text($person.name.slice(0,1));
							if (undefined != $person.color && null != $person.color){
								$no_image.css('background', '#'+$person.color);
							}
						}
	
						$tooltip = $('#profile-tooltip');
	
						// Show the popup
						Popper.createPopper($this[0], $tooltip[0], {
							placement: 'bottom-start',
						});
						
						// $tooltip.html('<p>Loading...</p>');
						$tooltip.show();
				},100);
		});
		$(document).on('mouseleave', '[popover-profileInfo]', function(e){
				// Mouse has left, set show variable to false
				window.shouldShowPopup = false;
				setTimeout(function(){
						// If the popup is visible, hide it
						hidePopupNow();
				}, 100);
		});
		
		$(document).on('mouseenter', '#profile-tooltip', function(e){
				// Mouse is inside the tooltip, don't hide it now
				window.shouldHidePopup = false;
		});
		
		$(document).on('mouseleave', '#profile-tooltip', function(e){
				window.shouldHidePopup = true;
				hidePopupNow();
		});
	
		function hidePopupNow(){
				if (window.shouldHidePopup)
						$('#profile-tooltip').hide();
		}
  

});